import { Item, LocalStorageApiTypes } from '../data-types'
type Cb = any

/**
 *     LocalStorage
 *   getInstance()
 */
export class LocalStorageApi {
  private static _instance: LocalStorageApi = new LocalStorageApi()
  order: { [key: string]: number }
  private readonly namePrefix: string

  constructor() {
    this.order = {}
    this.namePrefix = 'lastOrderOf'
    if (LocalStorageApi._instance !== undefined) {
      throw new Error(
        'Error: Instantiation failed: Use LocalStorageApi.getInstance() instead of new.'
      )
    }
    LocalStorageApi._instance = this
  }

  public static getInstance(): LocalStorageApi {
    return this._instance
  }

  /**
   *  item's
   * @returns    order
   */
  getLastOrder(type: LocalStorageApiTypes): number {
    const name = this.namePrefix + type
    let num
    if (typeof this.order[name] === 'undefined') {
      const str = localStorage.getItem(name)
      if (str === null) {
        num = 0
      } else {
        num = parseInt(str)
      }
      if (!isNaN(num)) {
        this.order[name] = num
      } else {
        this.order[name] = 0
      }
    }
    localStorage.setItem(name, String(this.order[name] + 1))
    return this.order[name]++
  }

  /**
   *   item  Localstorage
   * @param type ,    
   * @param item   item
   * @param cb    cb(items)
   * @returns  item
   */

  addItem<T extends Item>(type: LocalStorageApiTypes, item: T, cb: Cb = () => {}): T {
    const items = this.getItems(type)
    const newItem = { ...item, order: this.getLastOrder(type) }
    items.push(newItem)
    this.setItems(type, items)
    cb(items)
    return newItem
  }

  /**
   *   LocalStorage
   * @param type ,    
   * @param items   items
   * @param cb  cb(items)
   */
  setItems<T extends Item>(type: LocalStorageApiTypes, items: T[], cb: Cb = () => {}): T[] {
    localStorage.setItem(type, JSON.stringify(items))
    cb(items)

    return items
  }

  /**
   *    LocalStorage  items   .
   * @param type ,    
   * @param cb  cb(items)
   * @returns    items     
   */
  getItems<T extends Item>(type: LocalStorageApiTypes, cb: Cb = () => {}): T[] {
    const response = localStorage.getItem(type)
    if (response !== null) {
      const parsed = JSON.parse(response) as T[]

      let maxOrder = 0
      for (const i of parsed) {
        if (Boolean(i.order) && typeof i.order === 'number' && i.order > maxOrder) {
          maxOrder = i.order
        }
      }
      if (this.order[this.namePrefix + type] < maxOrder) {
        this.order[this.namePrefix + type] = maxOrder
      }

      cb(parsed)
      return parsed.map((it) => ({
        ...it,
        time: it.time != null ? new Date(it.time) : null
      }))
    }
    return []
  }

  /**
   *    item  ,   item  shallDelete = true.      .
   * @param type ,    
   * @param Item  item,   id
   * @param shallDelete  items  true
   * @param Cb    cb(item)
   * @returns boolean    
   */
  updateItemById<T extends Item>(
    type: LocalStorageApiTypes,
    item: T,
    shallDelete: boolean = false,
    cb: Cb = () => {}
  ): boolean {
    const items = this.getItems(type)
    if (items.length > 0) {
      const index = items.findIndex((p) => p.id === item.id)
      if (index >= 0) {
        if (shallDelete) {
          const newItems = [...items.slice(0, index), ...items.slice(index + 1)]
          cb(newItems)
          this.setItems(type, newItems)
          return true
        } else {
          const newItems = [
            ...items.slice(0, index),
            { ...items[index], ...item },
            ...items.slice(index + 1)
          ]
          this.setItems(type, newItems)
          cb(newItems)
          return true
        }
      }
      return false
    }
    return false
  }
}
